import { Injectable, Output, EventEmitter } from '@angular/core';
import { IfirstRequest, ITableData } from '../../../interfaces/IAssignment';

@Injectable({
  providedIn: 'root'
})
export class TableDataService {

  @Output() refreshTable: EventEmitter<any> = new EventEmitter();

  public getNormHours: string;
  public getNightHours: string;
  public getNormTarif: string;
  public getNightTarif: string;

  constructor() { }

  public tableInhale: string[] = ['hours', 'definition', 'costs'];

  roleId = window.localStorage.getItem('roleId');

  normTariff: number = 0;
  nightTariff: number = 0;//parseFloat("0").toFixed(2);
  weekendTariff: number = 0;
  totalTariff: number = 0;

  hourNorm: number = 0;
  hourNight: number = 0;
  hourTotal: number = 0;

  tableContent = [];

  public setData: ITableData[] = [
    { hours: "12", definition: 'Tarif Normal', costs: this.normTariff + "" },
    { hours: "11", definition: 'Tarif Nacht', costs: this.nightTariff + "" },
    { hours: "0", definition: 'Gesamt' + ' ', costs: this.totalTariff + "" },

  ];

  getData(firstRequest: IfirstRequest) {
    this.setData = [];

    var hours = 0;
    var costs = 0;

    var counterFirst=0;
    var counterSeccond=0;

    var bothDone=0;

    //builds the table with the values from the database
    firstRequest.costs.forEach(data => {
      switch (data.name) {
        case "N": this.setData.push({ hours: data.hours.toString(), definition: 'Normal', costs: this.round2Commas(data.customerCost).toString() + "" }); break;
        case "Ni": this.setData.push({ hours: data.hours.toString(), definition: 'Nacht', costs: this.round2Commas(data.customerCost).toString() + "" }); break;
        case "We": this.setData.push({ hours: data.hours.toString(), definition: 'Wochenende', costs: this.round2Commas(data.customerCost).toString() + "" }); break;
        case "VoucherA": this.setData.push({ hours: data.hours.toString(), definition: 'Tarif A', costs: this.round2Commas(data.customerCost).toString() + "" }); break;
        case "VoucherB": this.setData.push({ hours: data.hours.toString(), definition: 'Tarif B', costs: this.round2Commas(data.customerCost).toString() + "" }); break;
      }
      counterFirst++;
      if(firstRequest.costs.length==counterFirst){
        bothDone++;
      }
    })

    firstRequest.costs.forEach(data => {
      hours = hours + data.hours;
      costs = costs + data.customerCost;

      counterSeccond++;
      if(firstRequest.costs.length==counterSeccond){
        bothDone++;
      }
    })

    this.setData.push({ hours: hours.toString(), definition: 'Gesamt', costs: this.round2Commas(costs).toString() + "" })

    this.totalTariff = this.normTariff + this.nightTariff;
    this.hourTotal = this.hourNorm + this.hourNight;

    if(bothDone==2){
      this.refreshTable.emit(this.setData);
    }
      
  }

  round2Commas(input) {
    var reminder = input.toFixed(2);
    var back = Number(reminder);
    return back;
  }
}





