import { Component, OnInit } from '@angular/core';
import { Input } from '@angular/core'; 
import { Output, EventEmitter } from '@angular/core';


@Component({
  selector: 'app-basic-button',
  templateUrl: './basic-button.component.html',
  styleUrls: ['./basic-button.component.scss']
})
export class BasicButtonComponent implements OnInit {
  @Input() btnText: string; //any Text

  @Input() btnColor: string; //colorGreen, colorGrey defined

  @Input() btnDisabled: boolean;

  @Input() btnSize: string; //small, medium or big defined

  @Input() set hover(hover:string){ //set the color when cursor hover over the button
    switch(hover){
      case "blue": this.hoverColor="hoverBlue"; break;
      case "white": this.hoverColor="hoverWhite"; break;
      case "green": this.hoverColor="hoverGreen"; break;
      default: this.hoverColor="";
    }
  }

  @Output("clickFunction") clickFunction: EventEmitter<any> = new EventEmitter(); //any function from parent class will be triggered
  
  constructor() { }

  hoverColor:string="";

  ngOnInit(): void {
  }

 

}
