import { GetService } from './../../Requests/get.service';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { IValue, IRequest } from '../../../interfaces/IAssignment';


@Injectable({
  providedIn: 'root'
})

export class ChooseActivitiysService {
  /*  chooseActivitys: string[] = ['Veranstanltungsbesuch', 'Freizeitwege', 'ehrenamtliche Ttigkeit', 'Aufgaben im Rahmen der Elternsch.', 'sportliche Aktivitt', 'Feizeitbeschftigung zu Hause (Gste)', 'Urlaub (Reisen, Ausflge)', 'Arzt - und Krankenhausbesuche']*/

  //chooseActivitys: string[] = [];
  apiUrl = environment.apiUrl;

  chooseActivitys: IValue[] = [];

  constructor(private getService: GetService) { }

  getChooseActivitys() {
    this.chooseActivitys = [];
    this.getService.getChooseActivitysRecordNewUse()
      .toPromise()
      .then(data => {
        data.value.forEach(element => {
          this.chooseActivitys.push(element);
        }),
          error => {
            console.log(error);
          };
      });
    return this.chooseActivitys;
  }
}