import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { IHourSheet, IRequest } from 'src/app/interfaces/IAssignment';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class GetService {

  constructor(private http: HttpClient) { }

  apiUrl = environment.apiUrl;

  //#region customer
  /**
 * @returns IHourSheet /timesheet get Request
 */
  getTimeSheetHourSheetesCurrentMonth() {
    return this.http.get<IHourSheet[]>(this.apiUrl + `/timesheet`);
  }

  /**
   * @param date date from the selected row
   * @returns IHourSheet /timesheet get Request from sended date
   */
  getTimeSheetDateHourSheetsCurrentMonth(date: any) {
    return this.http.get<IHourSheet[]>(this.apiUrl + `/timesheet/${date}`);
  }

  switchBetweenUsersRecordNewUse() {
     // return this.http.get(this.apiUrl+'/customer');
     return this.http.get(this.apiUrl+'/assistant');
    
  }
  //#endregion

  //#region assistance
  /**
   * @returns table data for the hour sheets
   */
  getParticipateRecordNewUse() {
    return this.http.get<IRequest>(this.apiUrl + '/patask')
  }

  /**
   * @returns IRequest paymentmethod
   */
  getPasstBonsRecordNewUse() {
    return this.http.get<IRequest>(this.apiUrl + '/paymentmethod')
  }

  /**
   * @returns IRequest assistant
   */
  getAssistentRecordNewUse() {
    return this.http.get<IRequest>(this.apiUrl + '/assistant')
  }

  getChooseActivitysRecordNewUse() {
    return this.http.get<IRequest>(this.apiUrl+'/passttask')
  }
  //#endregion

  //#region service Point

  //#endregion

  //#region IVA

  //#endregion

}
