import { GetService } from './../../Requests/get.service';
import { FormDataService } from './form-data.service';
import { IExcelHourSheetsDetail, IValue } from 'src/app/interfaces/IAssignment';
import { EventEmitter, Injectable, Output } from '@angular/core';
import { IAssistant, ICustomer } from './../../../interfaces/IUser';
import { environment } from 'src/environments/environment';


@Injectable({
  providedIn: 'root'
})
export class HourSheetsdetailService {

  @Output() refreshTable: EventEmitter<any> = new EventEmitter();

  apiUrl = environment.apiUrl;

  roleId: number;
  columnDefs = [];
  getData: any;
  getUsers: any;
  constructor(private getService: GetService,
    private formData: FormDataService) { }

  generateInputs(getData, roleId) {
    this.columnDefs = [];
    this.roleId = roleId;

    this.getData = getData;
    this.switchBetweenUsers();
  }


  switchBetweenUsers() {
    this.getUsers = [];
    this.getService.switchBetweenUsersRecordNewUse()
      .toPromise()
      .then((data: IAssistant) => {
        data.value.forEach(element => {
          this.getUsers.push(element)
        });
      })
      .then(() => {
        this.buildData();
      });
  }

  //build the data for the table
  buildData() {
    var setData = [];
    var sheetData: IExcelHourSheetsDetail;

    this.getData.forEach(element => {
      var cost: string = "";
      var setUser: string;
      var number: number;
      var formData = this.formData.idsToNames(element); //form the getting date id`s to the elements N, NP1....

      switch (this.roleId) {
        case 2: cost = element.assistantCost;
          break;
        case 3: cost = element.customerCost;
          break;
        default: break;
      }

      //form the user Id to the username from the server 
      number = element.number;
      this.getUsers.forEach((user: IValue) => {
        if (user.id == element.id) {
          setUser = user.fullName;
        }
      });

      //builds the object for the excel
      sheetData = {
        N: formData.N,
        WE: formData.WE,
        NA: formData.NA,
        NP1: formData.NP1,
        NP2: formData.NP2,
        BonA: formData.BonA,
        BonB: formData.BonB,
        number: number,
        cost: this.formData.round2Commas(cost),
        fullName: setUser,
      }
      setData.push(sheetData);
    })
    this.refreshTable.emit(setData);
  }

}
