import { GetService } from './../../Requests/get.service';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { IValue } from '../../../interfaces/IAssignment';

@Injectable({
  providedIn: 'root'
})
export class PaastBonsService {

  apiUrl = environment.apiUrl;

  //  paastBons: string[] = ['Leistungsbon einlsen', 'Selbstzahler'];
  // paastBons: string[] = [];
  paastBons: IValue[] = [];


  constructor(private getService: GetService) { }

  getPaastBons() {
    this.paastBons = [];
    this.getService.getPasstBonsRecordNewUse()
      .toPromise()
      .then(data => {
        data.value.forEach(element => {
          this.paastBons.push(element);
        }),
          error => {
            console.log(error);
          };
      });
    return this.paastBons;
  }


}
