import { PostService } from './../../Requests/post.service';
import { GetService } from './../../Requests/get.service';
import { ToastsService } from './toasts.service';
import { RouteToService } from '../../../services/route-to.service';
import { TableDataService } from './table-data.service';
import { Injectable } from '@angular/core';
import { IfirstRequest, IValue } from '../../../interfaces/IAssignment';
import { environment } from 'src/environments/environment';


@Injectable({
  providedIn: 'root'
})
export class CustomersService {

  apiUrl = environment.apiUrl;

  assistent: IValue[] = [];
  firstRequest: IfirstRequest[] = [];

  constructor(private getService: GetService,
    private postService: PostService,
    private tableService: TableDataService,
    private routeToService: RouteToService,
    private toastr: ToastsService) { }


  getAssistent() {

    this.assistent = [];
    this.getService.getAssistentRecordNewUse()
      .toPromise()
      .then(data => {
        data.value.forEach((element) => {
          this.assistent.push(element);
        }),
          error => {
            console.log(error);
          };
      });

    return this.assistent;
  }

  //takes the input values to calculate to the server and show the request to the table on the side
  checkInputForTable(assistantid: number, pataskid: number, paymentmethodid: number, passttaskid: number, startTime: string, endTime: string) {
    this.firstRequest = [];
    this.postService.checkInputForTableRecordNewUse(assistantid, pataskid, paymentmethodid, passttaskid, startTime, endTime)
      .toPromise()
      .then((data: IfirstRequest[]) => {
        this.firstRequest = (data);
      })
      .then(() => {
        this.tableService.getData(this.firstRequest as any);
      })
    error => {
      console.log(error);
    }
  }

  //push the validation id to the server 
  pushInput() {
    this.postService.pushInputRecordNewUse(this.firstRequest['id'])
      .subscribe(() => {
      })
    error => {
      console.log(error);
      return;
    }
    this.toastr.seccondFormSuccess();
    this.routeToService.handleClick('start');
  }
}