 getCurrCredTotal(grantAmount:number, usedBons:number){
     let currCredTotal: number =  grantAmount - usedBons;
     return currCredTotal
 }

 getcurrCredMonthly(grantAmount:number, usedBons:number, usedCurrMonth:number ){
   // get current month
   let today:Date = new Date();
   this.currMonth = today.getMonth()+1;

   // get  Month when granting started
   let grantStartMonth = this.grantStart.getMonth()+1; //Rckgabewerte der Monate sind 0-11, daher +1
   
   // get Amount of MOnths left until current grant expires
   for(let i = 1; i<=12; i++){
     if(i < grantStartMonth){
       this.grantArray.shift(),
       this.grantArray.push(i);
     }    
   };
   let monthsLeft:number = 12 -(this.grantArray.indexOf(this.currMonth));//da Array index mit 0 anfngt msste man -1 rechnen, aber da der aktuelle Monat auch mitgezhlt wird ist das nicht ntig
 
   //get total Amount of Bon credit (of chosen tarif)
   let currCredTotal: number =  grantAmount - usedBons;
   // add amount of those Bons that have been used this actual month and divide by montLeft(we need the Number from the beginning of actual month)
   let currCredMonth : number =  (currCredTotal + usedCurrMonth)/monthsLeft;
   //subtract amount of Bons that have been used this actual month again
   currCredMonth-=usedCurrMonth;
   
   return Math.round(currCredMonth); // Ausgabe gerundet.
 }

