import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PostService {

  constructor(private http: HttpClient) { }

  apiUrl = environment.apiUrl;

  //#region customer


  //#endregion

  //#region assistance

  checkInputForTableRecordNewUse(assistantid: number, pataskid: number, paymentmethodid: number, passttaskid: number, startTime: string, endTime: string) {
    return this.http.post(this.apiUrl + '/assignment/calculate', {
      assistantid: assistantid, pataskid: pataskid, paymentmethodid: paymentmethodid,
      passttaskid: passttaskid == 0 ? null : passttaskid, from: startTime, to: endTime
    });
  }

  pushInputRecordNewUse(firstRequest:any) {
   return this.http.post<string>(this.apiUrl + '/assignment/confirm', firstRequest['id'])
  }

  //#endregion

  //#region service Point


  //#endregion

  //#region IVA


  //#endregion  

  //#region generally

  login(user:string, pw:string){
  return  this.http.post(this.apiUrl+'/authorization', { username: user, password: pw })
  }  

  //#endregion
}




