import { PopupTableComponent } from './popup-table/popup-table.component';
import { TimeServiceService } from '../../../services/time-service.service';
import { ProveTimeService } from './prove-time.service';
import { PaastBonsService } from './paast-bons.service';
import { ParticipateService } from './participate.service';
import { ChooseActivitiysService } from './choose-activitiys.service';
import { CustomersService } from './customers.service';
import { ToastsService } from './toasts.service';
import { TableDataService } from './table-data.service';
import { RouteToService } from '../../../services/route-to.service';
import { Component, OnInit } from '@angular/core';
import { IValue } from 'src/app/interfaces/IAssignment';
import { MatDialog } from '@angular/material/dialog';

//rate=> calculate!!
@Component({
  selector: 'app-record-new-use',
  templateUrl: './record-new-use.component.html',
  styleUrls: ['./record-new-use.component.scss']
})
export class RecordNewUseComponent implements OnInit {

  roleId: number = parseInt(window.localStorage.getItem('roleId'));

  //to show the inhale and the interface for the table
  tableDataSource = this.tableData.setData;
  displayedColumns: string[] = this.tableData.tableInhale;

  assistance: IValue[] = this.customersService.getAssistent();
  customers = [];

  participate: IValue[] = this.participateService.getParticipate();
  paastBons: IValue[] = this.paastBonsService.getPaastBons();
  chooseActivitys: IValue[] = this.chooseActivitysService.getChooseActivitys();

  buttonSave = "Besttigen"; //toggles the text on the button Save
  selfPayer = false; //for the entry in the database

  getTableData: any;

  datePickFirstDate: Date;
  datePickerSeccondDate: Date;
  pickStartTime: string;
  pickEndTime: string;

  dateTimepickerStart: any;
  dateTimepickerEnd: any;

  pickActivity: IValue = null;
  pickPaastBon: IValue = { id: 2, name: "" };
  pickPaastActivity: IValue = { id: 0, name: "" };

  pickAssistent: IValue;
  pickCustomer: IValue;

  isoDateStart: any;
  isoDateEnd: any;

  askNewDay = true;  // if the startTime from Timepicker is larger than the endTime, the TimePicker seccond is visible 
  participateDisabled = true; //if click on "teilhabe" the values can be choosen
  showTable = false; //shows if the user makes all input the table in the html



  constructor(
    private routeToService: RouteToService,
    private tableData: TableDataService,
    private toastsService: ToastsService,
    private customersService: CustomersService,
    private chooseActivitysService: ChooseActivitiysService,
    private participateService: ParticipateService,
    private paastBonsService: PaastBonsService,
    private proveTimeService: ProveTimeService,
    private timeService: TimeServiceService,
    public dialog: MatDialog
  ) {
    tableData.refreshTable.subscribe(newTable => {
      this.getTableData = newTable;
      this.openDialog();
    });
  }

  ngOnInit(): void {

  }

  /**
   * send the table constent end emit the result
   * @param this.tableDataSource
   */
  openDialog() {
    if (this.dialog.openDialogs.length != 0) return; // not open multible windows in dialog

    this.showTable = true;
    const dialogRef = this.dialog.open(PopupTableComponent, {

      width: '350px',
      data: this.getTableData//this.tableDataSource

    });
    dialogRef.afterClosed().subscribe(result => {
      this.showTable = false;

      if (result) {
        this.clickSave();
      }
    });
  }

  //click the Save button
  clickValidate() {
    // let proveTime = false;
    let proveChoose = false;

    if (this.proveTimeService.checkInputTime(this.dateTimepickerStart, this.dateTimepickerEnd)) { //this.datePickFirstDate, this.datePickerSeccondDate, this.pickStartTime, this.pickEndTime
      proveChoose = true;

      
      //refactor the input date and time to a date 
      /* this.isoDateStart = this.timeService.dateToIsoString(this.datePickFirstDate, this.pickStartTime);
      this.isoDateEnd = this.timeService.dateToIsoString(this.datePickerSeccondDate, this.pickEndTime); */

      this.isoDateStart = this.timeService.dateTimeToIsoString(this.dateTimepickerStart);
      this.isoDateEnd = this.timeService.dateTimeToIsoString(this.dateTimepickerEnd);
    
      switch (this.roleId) {
        case 2: proveChoose = this.checkInputAssistance(); break;
        case 3: proveChoose = this.checkInputCustomer(); break;
      }

      //with participate enabled
      if (this.participateDisabled == false && proveChoose) {
        if (this.pickPaastBon.name == "") {
          this.toastsService.noPaastBon();
          return;
        }

        if (this.pickPaastActivity.name == "") {
          this.toastsService.noPaastActivity();
          return;
        }
      }

      //prove the input values for the user
      this.proveSelfPayer(); //to prove the choose of selfpayer to push it to the database


      //checks the input for the table
      if (!this.showTable) { // makes the calcultation only if the user clicks besttigen
        switch (this.roleId) {
          case 2: this.checkInputForTableAssistent(); break;
          case 3: this.checkInputForTableCustomer(); break;
        }
      }
    }
  }

  //check the input if the roleId==2
  checkInputAssistance() {
    if (this.pickCustomer == undefined) {
      this.toastsService.noCustomer();
      return false;
    }
    return true;
  }

  //prove the input if the roleId ==3
  checkInputCustomer() {
    if (this.pickAssistent == undefined) {
      this.toastsService.noAssistance();
      return;
    }
    if (this.pickActivity == undefined) {
      this.toastsService.noActivity();
      return;
    }
    return true;
  }

  proveSelfPayer() {
    if (this.pickPaastBon.name != undefined) {
      if (this.pickPaastBon.name == "Selbstzahler") {
        this.selfPayer = true;
      } else {
        this.selfPayer = false;
      }
    }
  }

  //toggles the choosen participate  for "Teilhabe"
  checkParticipate(selected) {
    if (selected != undefined) {
      if (selected.name == "Teilhabe") {
        this.participateDisabled = false;
      } else {
        this.participateDisabled = true;
      }
    }
  }

  //push the input for the table
  checkInputForTableAssistent() {
  }

  //push the input for the table customer
  checkInputForTableCustomer() {
    this.customersService.checkInputForTable(this.pickAssistent.id, this.pickActivity.id,
      this.pickPaastBon.id, this.pickPaastActivity.id,
      this.isoDateStart, this.isoDateEnd);
  }

  clickAbord() {
    this.routeToService.handleClick('start');
  }

  clickSave() {
    //push the validation id to the server
    this.customersService.pushInput();
  }
}