import { RouteToService } from '../../../services/route-to.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  userName: string = window.localStorage.getItem('fullName');
  neuerEinsatzVisible: boolean = true;
  PASSTBonsVisible: boolean = true;
  textI4: string = "Einstellungen";
  itemAmount: string = "display4Items";
  roleId: string = localStorage.getItem('roleId');


  userNameSplit: string[] = [];

  routerLinks = ['/neuenEinsatzErfassen', '/neuenEinsatzBestaetigen', '/stundenblaetterAktuellerMonat', '/PASSTBonsGuthaben', '/benutzereinstellungen']


  constructor(
    private routeToService: RouteToService,
  ) { }

  ngOnInit(): void {
    if (this.proveLogin()) {
      this.userNameSplit = this.cutUsername();

      switch (this.roleId) {
        case "1": this.routerLinks = ['stundenblaetterAktuellerMonat', 'PASSTBonsGuthaben', 'benutzereinstellungen']; break;
        case "2": this.routerLinks = ['neuenEinsatzErfassen', 'stundenblaetterAktuellerMonat', 'benutzereinstellungen']; break;
        case "3": this.routerLinks = ['neuenEinsatzBestaetigen', 'stundenblaetterAktuellerMonat', 'PASSTBonsGuthaben', 'benutzereinstellungen']; break;
        default: break;
      }

    }
  }

  proveLogin() {
    if (localStorage.getItem('jwtToken') == null || localStorage.getItem('jwtToken') == undefined || localStorage.getItem('jwtToken') == "") {
      this.routeToService.handleClick('login');
      return false;
    }
    return true;

  }

  cutUsername() {
    return this.userName.split(" ");
  }

  routeTo(route: string) {
     this.routeToService.handleClick(route);
    
  }

  makeLabel(route:string){
    switch(route){
      case "stundenblaetterAktuellerMonat": return "Stundenbltter"; 
      case "PASSTBonsGuthaben":return "PASST Leistungsbons"; 
      case "benutzereinstellungen": return "Einstellungen"; 
      case "neuenEinsatzErfassen": return  "Einsatz Erfassen"; 
      case "neuenEinsatzBestaetigen": return "Einsatz Bestgtigen"; 
      default: return "noContent";
    }
  }

  makeImage(route:string){
    switch(route){
      case "stundenblaetterAktuellerMonat": return "assets/icons/PASST_sheets_grau.png"; 
      case "PASSTBonsGuthaben":return "assets/icons/PASST_Ticket_grau.png"; 
      case "benutzereinstellungen": return "assets/icons/passt_User_grau.png"; 
      case "neuenEinsatzErfassen": return  "assets/icons/PASST_write_grau.png"; 
      case "neuenEinsatzBestaetigen": return "assets/icons/PASST_write_grau.png"; 
      default: return "noContent";
    }
  }

  makeAlt(route:string){
    switch(route){
      case "stundenblaetterAktuellerMonat": return "Mehrere Bltter"; 
      case "PASSTBonsGuthaben":return "Ticket"; 
      case "benutzereinstellungen": return "Benutzer Bild"; 
      case "neuenEinsatzErfassen": return  "Block mit Stift"; 
      case "neuenEinsatzBestaetigen": return "Block mit Stift"; 
      default: return "noContent";
    }
  }

}

