import { NgxMatDateFormats } from '@angular-material-components/datetime-picker';
import { Component, Input, OnInit, Output, ViewChild, EventEmitter } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ThemePalette } from '@angular/material/core';
import * as moment from 'moment';


@Component({
  selector: 'app-ngx-date-time-picker',
  templateUrl: './ngx-date-time-picker.component.html',
  styleUrls: ['./ngx-date-time-picker.component.scss']
})
export class NgxDateTimePickerComponent implements OnInit {


  @ViewChild('picker') picker: any;

  @Input() outlined: string = "outline";

  @Input() minutesStep: string = "half"; //could set half or quater

  @Input() placeHolder: string = "placeholder";

  @Input() set colorPicker(color: string) {
    switch (color) {
      case 'white': this.inputClass = "inputWhite"; this.pickButtonClass = "pickButtonWhite"; break;
      default: this.inputClass = "inputDefault"; this.pickButtonClass = "pickButtonDefault"; break;
    }
  };

  inputClass: string = ""; //toggles the color for the input field
  pickButtonClass: string = ""; //toggles the color for the pick button 

  @Input() setTouchUi: boolean = true;

  @Output() getDateTime: EventEmitter<any> = new EventEmitter();

  public date: moment.Moment;
  public disabled = false;
  public showSpinners = true;
  public showSeconds = false;
  public touchUi = this.setTouchUi; // the display outlined in the view
  public enableMeridian = false; //am pm
  public minDate: moment.Moment;
  public maxDate: moment.Moment;
  public stepHour = 1;
  public stepMinute = 30;
  public stepSecond = 1;
  public color: ThemePalette = 'primary'; //'primary', 'accent', 'warn'

  initialisation: boolean = true;
  makeNewDateReminder: any;

  public formGroup = new FormGroup({
    date: new FormControl(null, [Validators.required]),
    date2: new FormControl(null, [Validators.required])
  })

  setPickerValue: any; //set the value after reform

  constructor() { }

  ngOnInit(): void {
    this.switchBetweenStepsMinutes();
  }

  /**
   * swift between minutesStep and set the selected input
   * default set half hour steps
   * initialisated the time in the input field and push it in the output
   * @input minutesStep
   */
  public switchBetweenStepsMinutes() {
    switch (this.minutesStep) {
      case 'none': this.dateControl = new FormControl(new Date()); this.stepMinute = 1; break;
      case 'half': this.dateControl = new FormControl(this.minutesHalfHourSteps(new Date())); if (this.initialisation) { this.initialisationTime(new Date()) }; this.stepMinute = 30; break;
      case 'quater': this.dateControl = new FormControl(this.minutesQuaterHourSteps(new Date())); this.stepMinute = 15; break;
      default: this.dateControl = new FormControl(new Date()); if (this.initialisation) { this.initialisationTime(new Date()) }; this.stepMinute = 1; break;
    }
  }

  /**
   * reform the get time and set the value for the visible field 
   * none dont form
   * @param event 
   */
  getTime(event: any) {

    switch (this.minutesStep) {
      case 'none': this.rebuildGetTime((event)); break;
      case 'half': this.rebuildGetTime(this.minutesHalfHourSteps(event)); break;
      case 'quater': this.rebuildGetTime(this.minutesQuaterHourSteps(event)); break;
      default: this.rebuildGetTime(this.minutesQuaterHourSteps(event)); break;
    }
  }

  public dateControl;  //2021,9,4,5,0,0
  public dateControlMinMax = new FormControl(new Date());


  /**
   * if minutes are higher than 15 small than 45 return 30
   * if minutes are higher than 45 small than 15 return 0
   * @param return minutes
   */
  public minutesHalfHourSteps(makeNewDate: any) {

    if (this.initialisation) {
      this.makeNewDateReminder = makeNewDate;
    }

    var minutes = moment(makeNewDate).minute();

    if (minutes > 15 && minutes < 45) {
      minutes = 30;
    } else {
      minutes = 0;
    }

    this.makeNewDateReminder.setMinutes(minutes);
    this.makeNewDateReminder.setSeconds(0);

    //on init give the initialisated date to the output
    if (this.initialisation) {
      this.initialisationTime(makeNewDate)
    }

    let reformMoment = moment(makeNewDate).toISOString(true);

    return reformMoment;
  }

  /**
   * if minutes are higher than 0 small than 15 return 0 so on
   * @param return minutes
   */
  public minutesQuaterHourSteps(makeNewDate) {
    var minutes = moment(makeNewDate).minute();


    if (minutes > 0 && minutes < 15) {
      minutes = 0;
    }
    if (minutes > 15 && minutes < 30) {
      minutes = 15;
    }
    if (minutes > 30 && minutes < 45) {
      minutes = 30;
    }
    if (minutes > 45 && minutes < 59) {
      minutes = 45;
    } else {
      minutes = minutes;
    }

    makeNewDate.setMinutes(minutes);
    makeNewDate.setSeconds(0);

    //on init give the initialisated date to the output
    if (this.initialisation) {
      this.initialisationTime(makeNewDate)
    }

    return makeNewDate;
  }

  initialisationTime(date: any) {
    this.getDateTime.emit(date);
    this.initialisation = false;
  }

  /**
    * set the reformed date to the value prop in the datepicker
    * build the output and visible value
    * @param date 
    */
  rebuildGetTime(date: any) {
    this.getDateTime.emit(date);
    this.setPickerValue = date;
  }

  onResize(event) {
    this.proveSize(event.target.innerWidth)
  }

  //if window is smaller than 1000px the touchUI (popup) is false
  proveSize(getWidth) {
    if (getWidth < 1000) {
      this.setTouchUi = false;

    } else {
      this.setTouchUi = true;
    }
  }
}


