import { TimeServiceService } from './time-service.service';
import { Injectable } from '@angular/core';
import { Workbook } from 'exceljs'; //in tsConfig.json import path:excelJs and => npm install --save exceljs@1.12.0
import * as fs from 'file-saver'; //npm install file-saver --save
@Injectable({
  providedIn: 'root'
})
export class GenerateExcelService {

  constructor(private timeService: TimeServiceService) { }

  //needs the header and body as a array 
  generateExcel(getHeader: any, getBody: any) {

    //Excel Title, Header, Data
    const title = 'Stundenblatt';
    const header = getHeader;

    const data = getBody//getBody;

    //Create workbook and worksheet
    let workbook = new Workbook();
    let worksheet = workbook.addWorksheet('HourSheet Data');
   
    //Add Row and formatting
    let titleRow = worksheet.addRow([title]);
    
    titleRow.font = { name: 'Arial', family: 4, size: 16, underline: 'double', bold: true }
    
    //space between title and content
    worksheet.addRow([]);
    worksheet.addRow([]);

    worksheet.mergeCells('A1:C2');
    
    //Blank Row 
    worksheet.addRow([]);
    //Add Header Row
    let headerRow = worksheet.addRow(header);

    // Cell Style : Fill and Border
    headerRow.eachCell((cell, number) => {
      cell.fill = {
        type: 'pattern',
        pattern: 'solid',
        fgColor: { argb: 'FFFFFF00' },
        bgColor: { argb: 'FF0000FF' }
      }
      cell.border = { top: { style: 'thin' }, left: { style: 'thin' }, bottom: { style: 'thin' }, right: { style: 'thin' } }
    })

    worksheet.addRows(data);

    worksheet.addRow([]);

    //Generate Excel File with given name
    workbook.xlsx.writeBuffer().then((data) => {
      let blob = new Blob([data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
      fs.saveAs(blob, `Stundenblatt-${this.timeService.getActualDate()}.xlsx`);
    })

  }
}
