import { FormDataService } from './form-data.service';
import { HourSheetsdetailService } from './hour-sheetsdetail.service';
import { GenerateExcelService } from './../../../services/generate-excel.service';
import { TimeServiceService } from './../../../services/time-service.service';
import { Component, OnInit } from '@angular/core';
import { IHourSheet, IDeclarationHourSheets, IExcelHourSheetsAssistance, IExcelHourSheetsDetail } from '../../../interfaces/IAssignment';
import { IExcelHourSheetsCustomer } from '../../../interfaces/IAssignment';
import { environment } from 'src/environments/environment';
import { ToastsService } from './toasts.service';
import { GetService } from '../../Requests/get.service';

@Component({
  selector: 'app-hour-sheets-archive',
  templateUrl: './hour-sheets-archive.component.html',
  styleUrls: ['./hour-sheets-archive.component.scss']
})
export class HourSheetsArchiveComponent implements OnInit {

  apiUrl = environment.apiUrl;

  excelBody: IDeclarationHourSheets[] = [];

  getData: IHourSheet[] = [];

  roleId: number = parseInt(window.localStorage.getItem('roleId'));

  tableClickable: boolean = true;

  setCheckbox: boolean = false;

  setSelectionType: string = "single";

  setColumns: any;

  setRowDetails: any;

  getSelectedRows: any;

  // Variables for side-nav
  subpageNavItems: string[] = ["Stundenbltter aktuell", "Stundenbltter Archiv"];
  subpageNavRoutes: string[] = ["/stundenblaetterAktuellerMonat", "/stundenblaetterArchiv",];

  constructor(
    private timeService: TimeServiceService,
    private generateExcelService: GenerateExcelService,
    private hourSheetsDetail: HourSheetsdetailService,
    private formData: FormDataService,
    private toastsService: ToastsService,
    private getService: GetService
  ) {
    hourSheetsDetail.refreshTable.subscribe(newTable => {
      //this.rowData = newTable;
      this.rows = [];
      newTable.forEach(element => {
        this.rows.push(element);
      });
    })

  }

  ngOnInit(): void {
    this.generateFirstInput();
  }

  setRows() {
    return this.rows;
  }

  rows: any = new Array<any>();

  /**
   * get the data from the timesheet url and fetch it in the getData
   * then form it with the formData-Service to an String element
   */
  generateFirstInput() {

    this.setColumns = [
      { name: 'Jahr', prop: 'year', orientation: "left" },
      { name: 'Monat', prop: 'month', orientation: "left" },
      { name: 'Eintrge', prop: 'number', orientation: "center" },

    ];

    this.setRowDetails = [
      { name: 'N', prop: "N" },
      { name: 'WE', prop: 'WE' },
      { name: 'NA', prop: 'NA' },
      { name: 'NP1', prop: 'NP2' },
      { name: 'NP2', prop: 'NP2' },
      { name: 'BonA', prop: 'BonA' },
      { name: 'BonB', prop: 'BonB' }
    ];
    this.switchBetweenUsers();

    this.tableClickable = true;
    this.setCheckbox = false;
    this.setSelectionType = "single";
    var getData = [];

    this.getService.getTimeSheetHourSheetesCurrentMonth()
      .toPromise()
      .then((data) => {
        // this.agGrid.api.setRowData(data);
        data.forEach(element => {
          getData.push(element)
        })
      })
      .then(() => {
        this.convertFirstInput(getData)
      })
  }

  convertFirstInput(getData) {
    var setData: IDeclarationHourSheets[] = [];
    getData.forEach((data) => {
      var formData = this.formData.idsToNames(data);
      setData.push({
        year: this.timeService.convertEnglishTime(data.timeSpan, true), month: this.timeService.convertEnglishTime(data.timeSpan, false), number: data.number, customerCost: this.formData.round2Commas(data.customerCost),
        assistantCost: this.formData.round2Commas(data.assistantCost),
        N: formData.N, WE: formData.WE, NA: formData.NA, NP1: formData.NP1,
        NP2: formData.NP2, BonA: formData.BonA, BonB: formData.BonB, timeToPush: data.timeSpan
      });
    })
    this.rows = setData;
  }

  /**
   * @param roleId generate an array of userser peek the role id
   */
  switchBetweenUsers() {
    switch (this.roleId) {
      case 2: this.setColumns.push({ name: 'Kosten in ', prop: 'assistantCost', orientation: "right" });
        break;
      case 3: this.setColumns.push({ name: 'Kosten in ', prop: 'customerCost', orientation: "right" });
        break;
      default: break;
    }
  }

  /**
   * 
   * @param data getSelected Rows setCheckbox tableClickable 
   * the first click start the new Table event the seccond and multiclick start the download button
   */
  getRowClicked(data) {
    this.generateSeccondInput();
    var getPushDate = data[0].timeToPush;
    if (this.tableClickable) {
      this.getService.getTimeSheetDateHourSheetsCurrentMonth(getPushDate)
        .toPromise()
        .then(data => {
          this.hourSheetsDetail.generateInputs(data, this.roleId);

        })
        .then(() => {
          this.setCheckbox = true;
          this.setSelectionType = "checkbox";
          this.tableClickable = false;
        })

    } else {
      this.getSelectedRows = data;
    }
  }
  generateSeccondInput() {

    this.setColumns = [
      { name: 'Name', prop: 'fullName', orientation: "left" },
      { name: 'Eintrge', prop: 'number', orientation: "center" },
      { name: 'Betrag in ', prop: 'cost', orientation: "right" }
    ];

    this.setRowDetails = [
      { name: 'N', prop: "N" },
      { name: 'WE', prop: 'WE' },
      { name: 'NA', prop: 'NA' },
      { name: 'NP1', prop: 'NP2' },
      { name: 'NP2', prop: 'NP2' },
      { name: 'BonA', prop: 'BonA' },
      { name: 'BonB', prop: 'BonB' }
    ];
  }

  //get the selected Row(s) with the checkbox clicked and the the download button
  rowsToExcel() {
    // const selectedNodes = this.agGridComponent.getSelectedRows();
    var header = [];
    var excelBody = [];
    var rowSelected= true;

    var excelAssistant: IExcelHourSheetsAssistance;
    var excelCustomer: IExcelHourSheetsCustomer;
    var excelDetail: IExcelHourSheetsDetail;

    this.setColumns.forEach(data => {
      header.push(data.name);
    })
    this.setRowDetails.forEach(data => {
      header.push(data.name);
    })

    // selectedNodes.forEach((data: any) => {
    /* switch (this.tableClickable) {
      case true:
        switch (this.roleId) {
          case 2:
            this.getSelectedRows.forEach((data: IExcelHourSheetsAssistance) => {
              excelAssistant =
              {
                year: data.year,
                month: data.month,
                number: data.number,
                assistantCost: data.assistantCost,
                N: data.N,
                WE: data.WE,
                NA: data.NA,
                NP1: data.NP1,
                NP2: data.NP2,
                BonA: data.BonA,
                BonB: data.BonB
              }
              excelBody.push(Object.values(excelAssistant));

            });
            break;
          case 3:
            this.getSelectedRows.forEach((data: IExcelHourSheetsCustomer) => {
              excelCustomer =
              {
                year: data.year,
                month: data.month,
                number: data.number,
                customerCost: data.customerCost,
                N: data.N,
                WE: data.WE,
                NA: data.NA,
                NP1: data.NP1,
                NP2: data.NP2,
                BonA: data.BonA,
                BonB: data.BonB,
              }
              excelBody.push(Object.values(excelCustomer));
            });
            break;
        }
        break;
      case false: */
    try {
      this.getSelectedRows.forEach((data: IExcelHourSheetsDetail) => {
        excelDetail =
        {
          fullName: data.fullName,
          number: data.number,
          cost: data.cost,
          N: data.N,
          WE: data.WE,
          NA: data.NA,
          NP1: data.NP1,
          NP2: data.NP2,
          BonA: data.BonA,
          BonB: data.BonB
        }
        excelBody.push(Object.values(excelDetail));
        rowSelected=true;
      })
    } catch {
      this.toastsService.noRowSelected();
      rowSelected=false;
    } finally {
      if(rowSelected){
        this.generateExcelService.generateExcel(header, excelBody);
      }
    }
  }

  
}
