import { Component, EventEmitter, HostListener, Input, OnInit, Output, ViewChild } from '@angular/core';
import { ColumnMode, SelectionType } from '@swimlane/ngx-datatable';

@Component({
  selector: 'app-ngx-table',
  templateUrl: './ngx-table.component.html',
  styleUrls: ['./ngx-table.component.scss'],
})
export class NgxTableComponent implements OnInit {

  @ViewChild('myTable') table: any;  //id of the ngx-datatable html

  @Input() setArrow: boolean = true; //set the arrow proberty in table

  @Input() setCheckbox: boolean = false; //set the checkbox proberty in tabel

  @Input() setSelectionType: any = "single"; //[checkbox] get the selectiontype for the event with the checkboxes

  @Input() limit: number = 5; //limit the showing rows in the tabel init with 5

  @Input() startLimit: number = 5;

  @Input() endLimit: number = 20;

  @Input() searchOff: boolean = true;

  @Input() set setRows(rows: any) {
    this.rows = rows;
    this.temp = rows;
  }

  @Input() topBarActivated: boolean = true;

  @Input() setColumns = [{ name: '', prop: '', orientation: "left" }];

  @Input() setRowDetails = [{ name: '', prop: '' }];

  @Output() rowSelect: EventEmitter<any> = new EventEmitter();


  constructor() { }

  rows: any = []; //rows in the table

  temp: any = []; //needet to select/search the rows 

  toggledBoth: number = 0; //arrow and row toggled

  limits: any = []; //5-20 in for loop initial

  selected: any = [];

  searchField: string = ""; //set value of the searfield

  searchFieldStrings: any = []; //string to show in searchfield

  searchFieldProperties: any = [];  //proberties of the searchfield ONLY 1 Property!!!

  searchFieldPropertyChange: string = ""; //string of proberty searchfield

  reminderArrow: boolean; //for the resize event

  reminderCheckbox: boolean; //for the resize event

  /**  Get the detail Page probertys
  *@param row detail
  */

  ColumnMode = ColumnMode;
  SelectionType = SelectionType; // to select one in this case or multi rows on click

  ngOnInit(): void {
    this.reminderArrow = this.setArrow;
    this.reminderCheckbox = this.setCheckbox;

    this.proveSize(window.innerWidth); //on init prove the size of the display

    for (var i = this.startLimit; i <= this.endLimit; i++) {
      this.limits.push(i);
    }

    //set searchfield proberties in selection and set the first field as the first entry
    this.setColumns.forEach((data) => {
      this.searchFieldProperties.push({ name: data.name, prop: data.prop, orientation: data.orientation });
      this.searchFieldStrings.push(data.name)
    });
    this.searchField = this.searchFieldStrings[0];
    this.searchFieldPropertyChange = this.searchFieldProperties[0].prop;
  }

  newLimit(getLimit) {
    this.limit = getLimit;
  }

  //on arrow click expand the details in the row
  toggleExpandRow(row) {
    this.toggledBoth++;
    this.table.rowDetail.toggleExpandRow(row);
  }

  /** 
  * @param select switch between single  and checkbox
  * on row clicked toggled the content 
  */
  onSelect({ selected }) {
    this.toggledBoth++;
    if (this.toggledBoth > 1 && this.setSelectionType == "single") {
    }

    if (this.toggledBoth == 1 && this.setSelectionType == "single") {
      this.rowSelect.emit(selected);
      this.selected = []; //flush the array if single clicked
    }

    //if multi and selected is empty after no selction the function call nothing
    this.toggledBoth = 0;

    if (this.setSelectionType == "checkbox") {
      if (selected.length == 0) {
        return;
      }
      this.rowSelect.emit(selected);
    }
  }

  //parse the limit string to an number
  setLimit(getLimit) {
    this.limit = getLimit;
  }

  setSearch(getSearch) {
    this.searchField = getSearch;
    this.searchFieldProperties.forEach(element => {
      if (element.name == getSearch) {
        this.searchFieldPropertyChange = element.prop;
      }
    });

  }

  updateFilter(event: any) {

    const val = event.toLowerCase();

    var propert = this.searchFieldPropertyChange;

    // filter our data
    const temp = this.temp.filter(function (d) {
      return d[propert].toString().toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rows = temp;
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }

  //if i set it on the top, it initialisates befor setArrow comes up and it throws an error
  @HostListener('window:resize', ['$event']) //for the resize event


  //if window is smaller than 700 pixel then the arrow and checkbox isn't showing else its initial
  onResize(event) {
    this.proveSize(event.target.innerWidth)
  }

  //prove the size of the display on init and on resize
  proveSize(getWidth) {
    if (getWidth < 700) {
      this.setCheckbox = false;
      this.setArrow = false;

    } else {
      this.setCheckbox = this.reminderCheckbox;
      this.setArrow = this.reminderArrow;
    }
  }
}
