import { Injectable } from '@angular/core';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class TimeServiceService {

  constructor() { }

  /**
   * needs date and time to form it to a iso string
   * @param date 
   * @param time 
   * @returns iso String with current Timezone
   */
  dateToIsoString(date, time) {

    console.log("date",date);
    console.log("time",time);
    

    let momentDate = moment(date);
    let momentTime = moment(time, 'HH:mm');

    momentDate.set({
      hour: momentTime.get('hour'),
      minute: momentTime.get('minute'),
      second: momentTime.get('second')
    });
    return momentDate.toISOString(true); //keep offset to isoString to true
  }

  /**
   * return the date string with a iso String
   * @param dateTime 
   * @returns iso String with current Timezone
   */
  dateTimeToIsoString(dateTime) {
    let momentDate = moment(dateTime);
    return momentDate.toISOString(true); //keep offset to isoString to true
  }

  getActualDate() {
    var actualDate = new Date();
    var dateTimeString = moment(actualDate).format("DD/MM/YYYY-HH/mm/ss");
    return dateTimeString;
  }

  replaceSigns(getString, getSign, replaceSign) {
    return getString.split(getSign).join(replaceSign);;
  }

  convertEnglishTime(getTime,getYear) {
    //splits the get Time string in an array
    var splitter = getTime.split("-");
    var year = splitter[0];
    var month = splitter[1];
    var germanMonths = ["Jnner", "Februar", "Mrz", "April", "Mai","Juni", "Juli", "August", "September", "Oktober", "November", "Dezember"];
    var returnGermanMonth:string;
   
    switch(month){
      case "January": returnGermanMonth=germanMonths[0]; break;
      case "February":returnGermanMonth=germanMonths[1]; break;
      case "March": returnGermanMonth=germanMonths[2];break;
      case "April":returnGermanMonth=germanMonths[3]; break;
      case "May":returnGermanMonth=germanMonths[4]; break;
      case "June": returnGermanMonth=germanMonths[5];break;
      case "July": returnGermanMonth=germanMonths[6];break;
      case "August":returnGermanMonth=germanMonths[7]; break;
      case "September": returnGermanMonth=germanMonths[8];break;
      case "October":returnGermanMonth=germanMonths[9]; break;
      case "November": returnGermanMonth=germanMonths[10];break;
      case "December": returnGermanMonth=germanMonths[11];break;
    }
    if(getYear){
      return year;
    }
      return returnGermanMonth;    
  }
}
