import { Component, OnInit } from '@angular/core';
import { Input } from '@angular/core'; 


@Component({
  selector: 'app-side-nav-subpages',
  templateUrl: './side-nav-subpages.component.html',
  styleUrls: ['./side-nav-subpages.component.scss']
})
export class SideNavSubpagesComponent implements OnInit {
  @Input() data : string[]; 
  subpAmount3 : boolean = false;
  @Input() route : string[];

  constructor() { }

  ngOnInit(): void {

    this.setSubpAmount();     

  }

  setSubpAmount(){
    if(this.data.length > 2){
      this.subpAmount3= true;
    }
  }
}
