import { ViewEncapsulation } from '@angular/core';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
@Component({
  selector: 'app-select-field',
  templateUrl: './select-field.component.html',
  styleUrls: ['./select-field.component.scss'],
})


//needs the interface with id and name to give back the selectet value as a string in the ui and a object in the get item
export class SelectFieldComponent implements OnInit {

  @Input() inputOutline: string = "outline"; //border over the field

  @Input() textAlignRight: boolean = false; //selector text placeholder left or right

  @Input() set textAlign(setTextAlign: string) {
    switch(setTextAlign){
      case "right": this.setTextAlign="right"; break;
      case "center": this.setTextAlign="center"; break;
      case "left": this.setTextAlign="left"; break;
      default: this.setTextAlign="left"; break;
    }
  }

  @Input() title: string = ""; // the default text in the field

  @Input() matSelect: any;  // the input with the user choosen value

  @Input() disabled: boolean;  //disabled the field

  @Input() value: any;  //gets the input object like => id:number, name:string or id, fullname...

  @Input() getkeyval: string = ""; // keyval to interate over the object and show it in the html

  @Input() inputAriaLabel: string = this.title;

  @Input() set textCenter(textCenter: boolean) {
    if (textCenter) {
      this.setTextCenter = "textCenter";
    }
  } //set the text to center

  @Input() set colorSelect(color: string) {
    switch (color) {
      case 'white': this.inputClass = "inputWhite"; break;
      default: this.inputClass = "inputDefault"; break;
    }
  };

  @Output() getItem: EventEmitter<string> = new EventEmitter();

  constructor() { }

  setTextAlign: string = "";

  inputClass: string = "inputDefault"; //default inherit in scss
  setTextCenter: string = "";

  ngOnInit(): void {
  }

  setValue() {
    this.getItem.emit(this.matSelect);
  }


}
