import { RouteToService } from './route-to.service';
import { Injectable, EventEmitter } from '@angular/core';
import { Output } from '@angular/core';


@Injectable({
  providedIn: 'root'
})
export class AuthService {


  @Output() getButtons : EventEmitter<any> = new EventEmitter();

  public jwtToken = window.localStorage.getItem("jwtToken");
  public roleId= window.localStorage.getItem("roleId");
  public username = window.localStorage.getItem("userName");
  public userId = window.localStorage.getItem("userId");
  public fullname = window.localStorage.getItem("fullName");

  constructor( private routeToService: RouteToService,
    ) { }

  checkJWT() {
    if (window.localStorage.getItem("jwtToken") != null) {
      this.getButtons.emit("getToken");
      this.routeToService.handleClick('home');
    }
    else {
      this.getButtons.emit("noToken");
      this.routeToService.handleClick('login');
    }
  }
}
