import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';

@Component({
  selector: 'app-tabs-module',
  templateUrl: './tabs-module.component.html',
  styleUrls: ['./tabs-module.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class TabsModuleComponent implements OnInit {

  @Input() getContent: any;  //get the Names of the Links

  @Input() getLinks: any;  //get the contentn of the Links

  constructor() { }


  ngOnInit(): void {
    this.linkNames = this.getContent;
    this.linkRoutes = this.getLinks;
    this.activeLink = this.linkNames[this.checkActualLink()];
  }

  linkNames: string[] = []; //'Stundenbltter aktuell', 'Stundenbltter Archiv'
  linkRoutes: string[] = []; //'/stundenblaetterAktuellerMonat', '/stundenblaetterArchiv'
  activeLink: any;

  clickedLink(linkName) {
    this.activeLink = linkName;
  }

  /**
   * @returns index of linksRoute catch 0
   */
  checkActualLink() {
    try {
      return this.linkRoutes.indexOf(window.location.pathname);
    } catch {
      return 0;
    }

  }

  setClickLink(linkName) {

    return this.linkRoutes[this.linkNames.indexOf(linkName)];
    // console.log(this.linkNames.indexOf[linkName]);
    // console.log(this.linkNames.indexOf(linkName));


  }

  // background: ThemePalette = undefined;

  /* toggleBackground() {
    this.background = this.background ? undefined : 'primary';
  }
 */
  /*  addLink() {
     this.links.push(`Link ${this.links.length + 1}`);
   } */
}
