import { ToastsService } from './toasts.service';
import { Injectable } from '@angular/core';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class ProveTimeService {

  constructor(private toastsService: ToastsService) { }

  //prove the validity of the input time
  checkInputTime(datePickFirstDate, datePickerSeccondDate) { //, pickStartTime, pickEndTime
    //prove start date
    if (moment(datePickFirstDate).isValid() == false) { //datePickFirstDate, dateFormat
      this.toastsService.noStartDate();
      return false;
    }

    //prove end date
    if (moment(datePickerSeccondDate).isValid() == false) { //, dateFormat
      this.toastsService.noEndDate();
      return false;
    }

    //prove the input start date with input end date with other day
    if (moment(datePickFirstDate, "DD-MM-YYYY").isAfter(moment(datePickerSeccondDate, "DD-MM-YYYY"))) {
      this.toastsService.startDateLargerEndDate();
      return false;
    }

    //prove the next day if starttime is smaller than end time and start date is the same as the end date
      if (!(moment(datePickerSeccondDate, "DD-MM-YYYY-HH-MM").isAfter(moment(datePickFirstDate, "DD-MM-YYYY-HH-MM")))) {
        this.toastsService.noEndDate();
        return false;
      }

    //prove if the end day is more than two days forward the start day
    if (moment(datePickerSeccondDate, "DD-MM-YYYY").diff(moment(datePickFirstDate, "DD-MM-YYYY"), 'days') > 2) {
      this.toastsService.seccondDateToFarInTheFuture();
      return false;
    }

    //prove if the 
    if (moment(datePickerSeccondDate, "DD-MM-YYYY-HH-MM").diff(moment(datePickFirstDate, "DD-MM-YYYY-HH-MM"), 'minutes')<=0) {
      this.toastsService.startEndTimeSame();
      return false;
    }

    return true;
  }
}
