import { Injectable } from '@angular/core';
import { IHoursPerRate } from '../../../interfaces/IAssignment';


export interface IidsToNames {
  N: number;
  WE: number;
  NA: number;
  NP1: number;
  NP2: number;
  BonA: number;
  BonB: number;
}

@Injectable({
  providedIn: 'root'
})
export class FormDataService {

  constructor() { }

  idsToNames(getData) {
    var N: number = 0;
    var WE: number = 0;
    var NA: number = 0;
    var NP1: number = 0;
    var NP2: number = 0;
    var BonA: number = 0;
    var BonB: number = 0;


    getData.hoursPerRate.forEach((getElement: IHoursPerRate) => {
      switch (getElement.id) {
        case 1: N = getElement.hours; break;
        case 2: WE = getElement.hours; break;
        case 3: NA = getElement.hours; break;
        case 4: NP1 = getElement.hours; break;
        case 5: NP2 = getElement.hours; break;
        case 6: BonA = getElement.hours; break;
        case 7: BonB = getElement.hours; break;
        default: break;
      }
    });

    return { N, WE, NA, NP1, NP2, BonA, BonB } as IidsToNames;
  }

  round2Commas(input) {
    var reminder = input.toFixed(2);
    //Number removed
    return (reminder);
  }
}
