import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { NgxMaterialTimepickerTheme, NgxMaterialTimepickerModule } from 'ngx-material-timepicker';

@Component({
  selector: 'app-time-picker',
  templateUrl: './time-picker.component.html',
  styleUrls: ['./time-picker.component.scss']
})
export class TimePickerComponent implements OnInit {
  @Input() title: string; //Text

  @Input() pickTime: string; //get the picked time

  @Input() minutesGap: string; // minutes gap between the picker minutes

  @Input() getName: string;

  @Input() set colorPicker(color:string){
    switch(color){
      case 'white' : this.inputClass="inputWhite"; this.arrowButtonClass="pickButtonWhite"; break;
      default : this.inputClass="inputDefault"; this.arrowButtonClass="pickButtonDefault"; break;
    }
  };

  inputClass:string=""; //toggles the color for the input field
  arrowButtonClass:string=""; //toggles the color for the pick button 

  @Output() time: EventEmitter<string> = new EventEmitter(); //get the time when clicked

  getTime="";

  constructor() { }

  ngOnInit(): void {
    
  }
  
  setTime(){
    this.time.emit(this.getTime);
  }
  
  colorTimePicker: NgxMaterialTimepickerTheme = {
    container: {
      bodyBackgroundColor: '#f4f4f4', //whtie
      buttonColor: '#1D71B8' //blue
    },
    dial: {
      dialBackgroundColor: '#1D71B8', //blue
    },
    clockFace: {
      clockFaceBackgroundColor: '#D3D3D3', //lightgray
      clockHandColor: '#95c11f',  //green
      clockFaceTimeInactiveColor: '#000000',
      clockFaceInnerTimeInactiveColor: '#000000' //black
    },
  };
  open(){
  }
}
