import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-search-field',
  templateUrl: './search-field.component.html',
  styleUrls: ['./search-field.component.scss']
})
export class SearchFieldComponent implements OnInit {

  @Input() inputOutline: string = "outline";

  @Input() disabled: boolean;  //disabled the field

  @Input() textCenter: boolean = false; //set the text to center

  @Input() inputLabel: string = "Suche (Eingabe)"; //set the default Label if field is empty

  @Input() set colorSelect(color:string){
    switch(color){
      case 'white' : this.inputClass="inputWhite";  break;
      default : this.inputClass="inputDefault";  break;
    }
  };

  @Output() getItem: EventEmitter<string> = new EventEmitter();

  constructor() { }

  inputClass:string="inputDefault"; //default inherit in scss

  ngOnInit(): void {

  }

  setValue(getValue) {
    this.getItem.emit(getValue.target.value); //set the complete word to the getItem output
  }

}
