import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { IValue } from '../../../interfaces/IAssignment';
import { GetService } from '../../Requests/get.service';


@Injectable({
  providedIn: 'root'
})

export class ParticipateService {

  apiUrl = environment.apiUrl;

  //participate: string[] = ['Pflege / Grundbedrfnisse', 'Leben zu Hause / Haushaltsf.', 'Teilhabe', 'Lebensorganisation'];
  // participate: string[] = [];
  participate: IValue[] = [];

  constructor(private getService: GetService) { }

  getParticipate() {
    this.participate = [];
    this.getService.getParticipateRecordNewUse()
      .toPromise()
      .then(data => {
        data.value.forEach(element => {
          this.participate.push(element);
        }),
          error => {
            console.log(error);
          };
      });
    return this.participate;
  }
}
