import { ColorElementsService } from './color-elements.service';
import { topBarInhales } from './../components/header/topbarInhale';
import { EventEmitter, Injectable, Output } from '@angular/core';
import { Router } from '@angular/router';


@Injectable({
  providedIn: 'root'
})
export class RouteToService {

  buttons: any;

  roleId: string = localStorage.getItem('roleId');

  @Output() getButtons: EventEmitter<any> = new EventEmitter();

  constructor(
    private router: Router,
    private colorService:ColorElementsService
  ) { }


  getIdFromLib = 0;

  /* with the roleId switches the three different header items in the topbarinhals.ts file */
  generateButtons(roleId) {
    var getRole = parseInt(roleId);
    switch (getRole) {
      case 1: this.buttons = topBarInhales.topBarInhales1; break;
      case 2: this.buttons = topBarInhales.topBarInhales2; break;
      case 3: this.buttons = topBarInhales.topBarInhales3; break;
      default: this.buttons = []; break; //needs an empty array to show no items in the header component
    }
    this.colorService.onInitActual(this.buttons);
  }

  //emits the change in the auth service to toggle the items in the header in the routToService component with the diffenent arrays
  proveButtons(but:string) {
    if (but == "getToken") {
      this.roleId = localStorage.getItem('roleId');
      this.generateButtons(this.roleId);
      this.colorService.onInitActual(this.buttons);
    
    }

    if (but == "noToken") {
      this.roleId = localStorage.getItem('roleId');
      this.generateButtons(this.roleId);
     
    }
  }
  refresh() {
    this.roleId = localStorage.getItem('roleId');
    this.generateButtons(this.roleId);
  }

/**
 * get the route as a string
 * foreach buttons and send the button with the click equals to route to the color Service
 * @param route 
 */
  handleClick(route: string) {
    try {
      this.router.navigate([`/${route}`]);
      if (route != "login") {
        this.buttons.forEach(element => {
          if(element.click==route){
            this.colorService.handleClickHeader(element);
          }
        });
      }
    } catch {
      console.log("url not found")
    }
  }
}
